.App {
  padding: 4px;
}

.toolbar {
  margin: 8px 0 8px 4px;
}

.toolbar strong {
  margin-right: 16px;
}

.toolbar select {
  margin-right: 4px;
}

.toolbar button {
  margin-right: 4px;
}

.iframe-list {
  display: flex;
}

.iframe {
  flex: 1;
  width: 100%;
  height: calc(100vh - 50px);
  border: none;
}
